/**
 * Applies styles for users in high contrast mode. Note that this only applies
 * to Microsoft browsers. Chrome can be included by checking for the `html[hc]`
 * attribute, however Chrome handles high contrast differently.
 */
/* vdl-color using default palette 'vdl-palette-1'
  Examples of use:
  vdl-color(15);

  to specify a different color palette:
  vdl-color(4, $some-other-palette);
 */
.vdl-card {
  border: 1px solid #e5e5e5;
  border-bottom-width: 2px;
  display: block;
  position: relative;
  padding: 24px;
  border-radius: 2px;
  font-family: Roboto, sans-serif;
  background-color: #fff;
  color: #414142; }
  @media screen and (-ms-high-contrast: active) {
    .vdl-card {
      outline: solid 1px; } }

.vdl-card-flat {
  box-shadow: none; }

.vdl-card-title, .vdl-card-subtitle, .vdl-card-content, .vdl-card-actions {
  display: block;
  margin-bottom: 16px; }

.vdl-card-title {
  font-size: 1.5rem;
  font-weight: 400; }

.vdl-card-subtitle {
  font-size: 14px; }

.vdl-card-content {
  font-size: 14px; }

.vdl-card-actions {
  margin-left: -16px;
  margin-right: -16px;
  padding: 8px 0; }
  .vdl-card-actions[align='end'] {
    display: flex;
    justify-content: flex-end; }

.vdl-card-image {
  width: calc(100% + 48px);
  margin: 0 -24px 16px -24px; }

.vdl-card-xl-image {
  width: 240px;
  height: 240px;
  margin: -8px; }

.vdl-card-footer {
  position: absolute;
  width: 100%;
  min-height: 5px;
  bottom: 0;
  left: 0; }

.vdl-card-actions .vdl-button, .vdl-card-actions .vdl-raised-button {
  margin: 0 4px; }

.vdl-card-header {
  flex: 1 1 auto;
  display: flex;
  flex-direction: row;
  height: auto;
  margin: -8px 0 16px 0; }

.vdl-card-header-text {
  height: auto;
  margin: 0 8px;
  flex: 1 1 auto; }

.vdl-card-header-content {
  flex: 0 1 auto; }

.vdl-card-header-image {
  flex: 0 1 auto; }

.vdl-card-avatar {
  height: auto;
  width: auto; }

.vdl-card-header-actions {
  flex: 0 1 auto;
  background-color: #fff; }

.vdl-card-sm-image, .vdl-card-vdl-image, .vdl-card-lg-image {
  margin: -8px 0; }

.vdl-card-title-group {
  display: flex;
  justify-content: space-between;
  margin: 0 -8px; }

.vdl-card-sm-image {
  width: 80px;
  height: 80px; }

.vdl-card-vdl-image {
  width: 112px;
  height: 112px; }

.vdl-card-lg-image {
  width: 152px;
  height: 152px; }

@media (max-width: 600px) {
  .vdl-card {
    padding: 24px 16px; }
  .vdl-card-actions {
    margin-left: -8px;
    margin-right: -8px; }
  .vdl-card-image {
    width: calc(100% + 32px);
    margin: 16px -16px; }
  .vdl-card-title-group {
    margin: 0; }
  .vdl-card-xl-image {
    margin-left: 0;
    margin-right: 0; }
  .vdl-card-header {
    margin: -8px 0 0 0; } }

.vdl-card > :first-child, .vdl-card-content > :first-child {
  margin-top: 0; }

.vdl-card > :last-child, .vdl-card-content > :last-child {
  margin-bottom: 0; }

.vdl-card-image:first-child {
  margin-top: -24px; }

.vdl-card > .vdl-card-actions:last-child {
  margin-bottom: -16px;
  padding-bottom: 0; }

.vdl-card-actions .vdl-button:first-child,
.vdl-card-actions .vdl-raised-button:first-child {
  margin-left: 0;
  margin-right: 0; }

.vdl-card-title:not(:first-child), .vdl-card-subtitle:not(:first-child) {
  margin-top: -4px; }

.vdl-card-header .vdl-card-subtitle:not(:first-child) {
  margin-top: -8px; }

.vdl-card > .vdl-card-xl-image:first-child {
  margin-top: -8px; }

.vdl-card > .vdl-card-xl-image:last-child {
  margin-bottom: -8px; }
